  overflow: hidden;
  width: 100%;
  margin: 0;
  padding: 0;
  margin-bottom: 20px;
  text-align: center;
  position: relative;
  display: table;
  table-layout: fixed;
  /* the magic dust that ensures equal width */
  /*CSS counters to number the steps*/
  counter-reset: step;
}

.fluentform .ff-step-titles li {
  display: table-cell;
  position: relative;
  list-style-type: none;
  color: #333;
  font-size: 12px;
  width: auto;
  padding: 0px 10px;
  vertical-align: top;
}

